import Swiper from 'swiper'
import gsap from 'gsap'
import ScrollTrigger from 'gsap/dist/ScrollTrigger'
import GallerySlide from '@/components/Blocks/GallerySlide.vue'
import ArticleCard from '@/components/Card/ArticleCard.vue'

export default {
    name: 'PageIndex',
    components: { GallerySlide, ArticleCard },
    meta: {
        loading: true,
    },
    async asyncData ({ $config, $axios, route, error }) {
        try {
            let { data } = await $axios.$get(`${$config.APP_BACKEND_API}/wp-json/api/index`)
            const banner = data.banner.map((el) => {
                return {
                    ...el,
                    year: new Date(el.date).getFullYear().toString(),
                    date_str: `${new Date(el.date).getMonth() + 1}.${new Date(el.date).getDate()}`,
                }
            })
            const projects = data.article_list[0]
            const courses = data.article_list[1]
            data = { ...data, banner }

            const seo = data.seo

            return {
                data,
                projects,
                courses,
                seo,
            }
        } catch (e) {
            console.log(e)
            error({ statusCode: 404, message: 'Post not found' })
            return {
                data: {},
                projects: {},
                courses: {},
                seo: {},
            }
        }
    },
    data () {
        return {
            fields: '',
            newsActive: 0,
            isFooterHide: false,
            isMainDecoShow: true,
            tl1: null,
            tl2: null,
            tl3: null,
            tl4: null,
        }
    },
    head () {
        return {
            title: this.seo.title,
            meta: [
                { hid: 'description', name: 'description', content: this.seo.desc },
                { hid: 'og:title', property: 'og:title', content: this.seo.title },
                { hid: 'og:description', property: 'og:description', content: this.seo.desc },
                { hid: 'og:url', property: 'og:url', content: `${process.env.APP_URL}` },
                { hid: 'og:site_name', property: 'og:site_name', content: this.seo.title },
                { hid: 'og:image', property: 'og:image', content: this.seo.thumb ? this.seo.thumb : `${process.env.APP_URL}/og_img.jpg` },
                { hid: 'og:image:alt', property: 'og:image:alt', content: this.seo.desc },
                { hid: 'twitter:description', name: 'twitter:description', content: this.seo.desc },
                { hid: 'twitter:title', name: 'twitter:title', content: this.seo.title },
                { hid: 'twitter:image', name: 'twitter:image', content: this.seo.thumb ? this.seo.thumb : `${process.env.APP_URL}/og_img.jpg` },
            ],
        }
    },
    computed: {
        loadingConfig () {
            return this.$store.state.loadingConfig
        },
        isLoading () {
            return this.$store.getters.isLoading
        },
        socialLink () {
            return this.$store.state.globalOptions.options.social_link
        },
    },
    created () {
        this.$store.commit('CHANGE_LOADING_TYPE', 'LOADING_TYPE_AJAX')
    },
    mounted () {
        this.scrollT1()
        this.scrollT2()
        this.scrollT3()
        this.scrollT4()
        const deco = this.$refs.deco
        const swiper = new Swiper('.index__banner__swiper', {
            slidesPerView: 1,
            // autoplay: {
            //     delay: 3000,
            // },
            loop: this.data.banner.length > 1,
            navigation: {
                nextEl: '.index__banner__swiper-button-next',
                prevEl: '.index__banner__swiper-button-prev',
            },
            pagination: {
                el: '.index__banner__swiper-pagination',
                clickable: true,
                renderBullet (index, className) {
                    return '<span class="' + className + ' swiper-pagination__item"><span class="icon-star"></span><span class="icon-circle"></span></span>'
                },
            },
        })
        swiper.on('slideChange', function () {
            deco.dataset.animate = swiper.realIndex + 1
        })

        window.addEventListener('scroll', this.scroll)
        window.addEventListener('resize', this.restartScrollT3)
    },
    beforeDestroy () {
        this.tl1.kill()
        this.tl2.kill()
        this.tl3.kill()
        this.tl4.kill()
        window.removeEventListener('scroll', this.scroll)
        window.removeEventListener('resize', this.restartScrollT3)
    },
    methods: {
        scroll () {
            if (window.scrollY > 150) {
                this.isFooterHide = true
            } else {
                this.isFooterHide = false
            }
        },
        switchNews (e) {
            this.newsActive = +e.target.dataset.id
        },
        scrollT1 () {
            this.tl1 = gsap.timeline({
                scrollTrigger: {
                    trigger: '.o-index-case.normal .container',
                    pin: false,
                    start: 'top bottom-=20%',
                    end: '+=255',
                    scrub: 2,
                    snap: false,
                },
            })
            this.tl1.fromTo('.o-index-case.normal .js-case-col__2', { y: -255 }, { y: 0 })
        },
        scrollT2 () {
            this.tl2 = gsap.timeline({
                scrollTrigger: {
                    trigger: '.o-index-case.invert .container',
                    pin: false,
                    start: 'top bottom-=20%',
                    end: '+=255',
                    scrub: 2,
                    snap: false,
                },
            })
            this.tl2.fromTo('.o-index-case.invert .js-case-col__2', { y: -255 }, { y: 0 })
        },
        scrollT3 () {
            const boxX = document.querySelector('.o-index-about__content__box').getBoundingClientRect().x
            const wrapperWidth = document.querySelector('.o-index-about__main-deco__wrapper').clientWidth
            const oriLeft = 0.5 * (boxX - wrapperWidth)
            this.tl3 = gsap.timeline({
                scrollTrigger: {
                    id: 'o-index-about__main-deco',
                    trigger: '.o-index-about__main-deco',
                    endTrigger: '#scroll-end',
                    pin: true,
                    start: 'center center',
                    end: 'top top',
                    scrub: true,
                    snap: false,
                },
            })
            this.tl3
                .set('.o-index-about__main-deco__wrapper', { left: oriLeft + 'px' }, 't1')
                .fromTo('.o-index-about__main-deco__wrapper__cover', { opacity: 1 }, { opacity: 0 }, 't1')
                .to('.o-index-about__main-deco__wrapper__left', { rotation: 180, left: '100%', width: 221, height: 510, y: 85 }, 't2')
                .to('.o-index-about__main-deco__wrapper__right', { rotation: -180, width: 147, height: 340, x: 0 }, 't2')
                .to('.o-index-about__main-deco__wrapper', { width: '50%' }, 't2')
                .to('.o-index-about__main-deco__wrapper', { left: '50%' }, 't2')
                .to('.o-index-about__main-deco__wrapper__left', { width: 150, height: 347, y: -20 }, 't3')
                .to('.o-index-about__main-deco__wrapper__right', { width: 120, height: 279 }, 't3')
                .to('.o-index-about__main-deco__wrapper', { top: '20%' }, 't3')
                .set('.o-index-about__main-deco__wrapper__left > svg', { transformOrigin: 'right' }, 't4')
                .set('.o-index-about__main-deco__wrapper__right > svg', { transformOrigin: 'left' }, 't4')
                .to('.o-index-about__main-deco__wrapper__left > svg', { rotation: 1080 }, 't4')
                .to('.o-index-about__main-deco__wrapper__right > svg', { rotation: 720 }, 't4')
                .to('.o-index-about__main-deco__wrapper__left', { width: 30, height: 70 }, 't4')
                .to('.o-index-about__main-deco__wrapper__right', { width: 15, height: 35, y: -36 }, 't4')
                .to('.o-index-about__main-deco__wrapper', { top: '75%' }, 't4')
                .to('.o-index-about__main-deco__wrapper__left', { opacity: 0 }, 't4+=75%')
                .to('.o-index-about__main-deco__wrapper__right', { opacity: 0 }, 't4+=75%')
        },
        scrollT4 () {
            this.tl4 = gsap.timeline({
                scrollTrigger: {
                    trigger: '.o-index-about__content__box',
                    pin: true,
                    start: 'center+=235 center',
                    scrub: true,
                    snap: false,
                    onEnter: () => {
                        this.$refs.aboutContent.dataset.content = 2
                        this.$refs.aboutContent.querySelectorAll('.swiper-pagination__item').forEach((el, i) => {
                            el.classList.remove('swiper-pagination-bullet-active')
                            if (i === 1) {
                                el.classList.add('swiper-pagination-bullet-active')
                            }
                            gsap.timeline()
                                .to('.o-index-about__main-deco__wrapper', {
                                    x: -50,
                                    opacity: 0,
                                    duration: 0.2,
                                    ease: 'linear',
                                    onComplete: () => {
                                        document.querySelector('.o-index-about__main-deco__wrapper__cover').dataset.image = '2'
                                    },
                                })
                                .to('.o-index-about__main-deco__wrapper', { x: 0, opacity: 1, duration: 0.2, ease: 'linear' })
                        })
                    },
                    onLeaveBack: () => {
                        this.$refs.aboutContent.dataset.content = 1
                        this.$refs.aboutContent.querySelectorAll('.swiper-pagination__item').forEach((el, i) => {
                            el.classList.remove('swiper-pagination-bullet-active')
                            if (i === 0) {
                                el.classList.add('swiper-pagination-bullet-active')
                            }
                            gsap.timeline()
                                .to('.o-index-about__main-deco__wrapper', {
                                    x: -50,
                                    opacity: 0,
                                    duration: 0.2,
                                    ease: 'linear',
                                    onComplete: () => {
                                        document.querySelector('.o-index-about__main-deco__wrapper__cover').dataset.image = '1'
                                    },
                                })
                                .to('.o-index-about__main-deco__wrapper', { x: 0, opacity: 1, duration: 0.2, ease: 'linear' })
                        })
                    },
                },
            })
        },
        restartScrollT3 () {
            const st3 = ScrollTrigger.getById('o-index-about__main-deco')
            st3.kill()
            this.tl3.kill()
            this.isMainDecoShow = false
            setTimeout(() => {
                this.isMainDecoShow = true
            }, 500)
            setTimeout(() => {
                this.scrollT3()
            }, 750)
        },
    },
}
